$swiper-bullet-border-color: $White;
$swiper-bullet-active-bg: $CodGray;
$product-detail-brand-color: $CodGray;
$product-detail-name-color: $CodGray;
$discount-badge-bg: $Cinnabar;
$discount-badge-color: $White;
$isFreeCargo-border-color: $Alto;
$isFreeCargo-icon-color: $Niagara;


.swiper-slide {
  display: flex;
}
.swiper-slide-img {
  width: 100%;
  // max-width: 500px;
}
.swiper-pagination-bullets {
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-pagination-bullet {
  width: 10px;
  height: 10px;

  border: 1px solid $swiper-bullet-border-color;

  background-color: rgba(29,29,29,.5);

  opacity: 1;
}
.swiper-pagination-bullet-active {
  width: 14px;
  height: 14px;

  background-color: $swiper-bullet-active-bg;
}
.swiper-button-next,
.swiper-button-prev {
  background-image: url("data:image/svg+xml;base64,");
}
.swiper-button-next {
  transform: rotate(180deg);
}

.o-product {
  &__detail {
    margin: $lg;
  }
  &__brand {
    margin-bottom: $xs;

    font-size: $fs-16;
    font-weight: $bold;
    line-height: 22px;
    &Link {
      color: $product-detail-brand-color;
    }
  }
  &__name {
    padding-bottom: $xl;

    color: $product-detail-name-color;

    font-size: $fs-16;
    font-weight: $regular;
    line-height: 22px;
  }
  &__isFreeCargo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

    padding: $xl 0;
    border-top: .5px solid $isFreeCargo-border-color;

    font-size: $fs-16;
  }
  &__freeCargoIcon {
    width: 26px;

    margin-right: $md;

    fill: $isFreeCargo-icon-color;
  }
}

.a-discountBadge {
  position: absolute;
  z-index: z("ground-floor");

  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  min-width: 30px;

  padding: 0 $md;
  margin-top: -50px;

  background-color: $discount-badge-bg;
  color: $discount-badge-color;

  font-size: $fs-14;
  font-weight: $bold;
  line-height: 20px;
}
